import axios, {AxiosInstance} from "axios";
import * as Routes from "../../routes";
import {BoardId} from "../model/Board";
import {convertNodeToJs, Node} from "../model/Graph";
import {Event} from "./Cody/Event";
import {List} from "immutable";

export const NO_CODY_CONNECTION = "No connection to Cody. Try to connect with him first!";

type CodyServer = AxiosInstance;

interface Session {
    nodes: Node[];
    currentNodeIndex: number;
    boardId: BoardId;
}

const printCodyResponse = (res: CodyResponse) => {
    const cody = (typeof res.cody === 'string')? [res.cody] : res.cody as [];
    const details = (typeof res.details === 'string')? [res.details] : res.details as [];
    const type = res.type? res.type : CodyResponseType.Info;

    switch (type) {
        case CodyResponseType.Info:
            if(cody.length === 1) {
                cody[0] = '%c'+cody[0];
                cody.push('color: #32c0e7;font-weight: bold')
            }
            console.info('%c🐵: '+cody[0], 'color: #414141', ...cody.slice(1));
            if(res.details) {
                console.log('%c🐵: '+details[0], 'color: #414141', ...details.slice(1));
            }
            break;
        case CodyResponseType.Error:
            if(cody.length === 1) {
                cody[0] = '%c'+cody[0];
                cody.push('color: color: #f31c30;font-weight: bold')
            }
            console.error('%c🐵: '+cody[0], 'color: #414141', ...cody.slice(1));
            if(res.details) {
                console.error('%c🐵: '+details[0], 'color: #414141', ...details.slice(1));
            }
            break;
        case CodyResponseType.Warning:
        case CodyResponseType.Question:
            if(cody.length === 1) {
                cody[0] = '%c'+cody[0];
                cody.push('font-weight: bold')
            }
            console.warn('%c🐵: '+cody[0], 'color: #414141', ...cody.slice(1));
            if(res.details) {
                console.warn('%c🐵: '+details[0], 'color: #414141', ...details.slice(1));
            }
            break;
        default:
            console.log('%c🐵: '+cody[0], 'color: #414141', ...cody.slice(1));
            if(res.details) {
                console.log('%c🐵: '+details[0], 'color: #414141', ...details.slice(1));
            }
    }
}

const printError = (error: string) => {
    console.error(error);
}

const printWarning = (warn: string) => {
    console.warn(warn);
}

const sortNodes = (nodes: Node[]): Node[] => {
    const sortedNodes: Node[] = [];

    const nodesWithoutSourceInNodeList = nodes.filter(n => {
        if(n.getSources().count() === 0) {
            return true
        }

        let allSourcesNotInNodeList = true;

        n.getSources().forEach(source => {
            if(nodes.find(orgN => orgN.getId() === source.getId())) {
                allSourcesNotInNodeList = false;
            }
        })

        return allSourcesNotInNodeList;
    });

    sortedNodes.push(...nodesWithoutSourceInNodeList);

    nodesWithoutSourceInNodeList.forEach(nodeNoSource => {
        addTargetsToSortedNodesList(nodeNoSource.getTargets(), nodes, sortedNodes);
    })

    return sortedNodes;
}

const addTargetsToSortedNodesList = (targets: List<Node>, originalNodes: Node[], sortedNodes: Node[], nestingLevel = 0): void => {
    targets.forEach(target => {
        if(originalNodes.find(orgN => orgN.getId() === target.getId())) {
            const targetInSortedNodes = sortedNodes.find(sN => sN.getId() === target.getId());

            if(!targetInSortedNodes) {
                sortedNodes.push(target);
            } else {
                // Target should always come after all of its sources
                sortedNodes.splice(sortedNodes.indexOf(targetInSortedNodes), 1);
                sortedNodes.push(target);
            }

        }

        nestingLevel++;

        if(nestingLevel < 100) {
            addTargetsToSortedNodesList(target.getTargets(), originalNodes, sortedNodes, nestingLevel);
        }
    })
}

let currentSession: Session | undefined;
let codyResponsePrinter = printCodyResponse;
let errorPrinter = printError;
let warningPrinter = printWarning;

const triggerCody: TriggerCody = (boardId: BoardId, nodes, currentNodeIndex?: number) => {
    if(nodes.length === 0) {
        warningPrinter("Selection is empty. Nothing to do for Cody!");
        return false;
    }

    if(typeof currentNodeIndex === 'undefined' && currentSession) {
        codyResponsePrinter({
            cody: "Wait, wait, wait! I'm still working on last session.",
            details: ["You can stop me by pressing %cESC", "background-color: rgba(251, 159, 75, 0.2)"],
            type: CodyResponseType.Warning
        });
        return false;
    }

    if(IIO.cody) {
        if(typeof currentNodeIndex === 'undefined') {
            currentSession = {nodes, currentNodeIndex: 0, boardId};

            nodes = sortNodes(nodes);

            triggerCody(boardId, nodes, 0);
        } else {
            const deeplinkFactory = Routes.makeWorkspaceDeeplinkFacory(boardId);

            IIO.cody!.post(Event.ElementEdited, {node: convertNodeToJs(nodes[currentNodeIndex])}).then(
                res => {
                    if(res.status !== 200) {
                       errorPrinter("Did not receive a valid response from cody. " + JSON.stringify(res));
                       return;
                    }
                    const codyRes = res.data;
                    codyResponsePrinter(codyRes);

                    if(codyRes.type !== CodyResponseType.Question
                        && codyRes.type !== CodyResponseType.StopSession
                        && codyRes.type !== CodyResponseType.Error
                        && (currentNodeIndex + 1) < nodes.length) {
                        currentSession!.currentNodeIndex = currentNodeIndex + 1;
                        triggerCody(boardId, nodes, currentNodeIndex + 1);
                    }

                    if(codyRes.type === CodyResponseType.StopSession
                        || codyRes.type === CodyResponseType.Error
                        || (currentNodeIndex + 1 === nodes.length && codyRes.type !== CodyResponseType.Question)) {
                        currentSession = undefined;
                    }
                },
                reason => {
                    if(typeof reason !== 'string') {
                        if(typeof reason.toString === 'function') {
                            reason = reason.toString();
                        } else {
                            reason = 'Unknown Error'
                        }
                    }

                    errorPrinter(reason)
                }
            );
        }
        return true;
    }

    errorPrinter(NO_CODY_CONNECTION);
    return false;
}

export const IIO: IIO = {
    connect: {
        Cody: (uri: string, user: string) => {
            if(IIO.cody) {
               IIO.disconnect();
            }

            IIO.cody = axios.create({
                baseURL:  uri + '/messages/',
            })

            IIO.cody.post<CodyResponse>(Event.IioSaidHello, {user}).then(
                res => {
                    if(res.status !== 200) {
                        errorPrinter("Did not receive a valid response from cody. " + JSON.stringify(res));
                        return;
                    }

                    codyResponsePrinter(res.data)
                },
                reason => codyResponsePrinter({
                    cody: 'Failed to reach out to cody',
                    details: [reason],
                    type: CodyResponseType.Error
                })
            );
            return true;
        }
    },
    disconnect: () => {
        if(IIO.cody) {
            IIO.cody = undefined;
            currentSession = undefined;
            codyResponsePrinter = printCodyResponse;
            errorPrinter = printError;
            warningPrinter = printWarning;
        }
    },
    reply: (reply: any) => {
        if(IIO.cody) {
            IIO.cody.post(Event.UserReplied, {reply}).then(
                res => {
                    if(res.status !== 200) {
                        errorPrinter("Did not receive a valid response from cody. " + JSON.stringify(res));
                        return;
                    }

                    const codyRes = res.data;

                    codyResponsePrinter(codyRes);

                    if(codyRes.type !== CodyResponseType.Question &&
                        codyRes.type !== CodyResponseType.StopSession &&
                        codyRes.type !== CodyResponseType.Error && currentSession)
                    {
                        const {nodes, currentNodeIndex} = currentSession;
                        if((currentNodeIndex + 1) < nodes.length) {
                            currentSession.currentNodeIndex = currentNodeIndex + 1;
                            triggerCody(currentSession.boardId, nodes, currentNodeIndex + 1);
                        }

                    }

                    if(codyRes.type === CodyResponseType.StopSession
                        || codyRes.type === CodyResponseType.Error
                        || (currentSession && (currentSession.currentNodeIndex + 1) === currentSession.nodes.length)
                    ) {
                        currentSession = undefined;
                    }
                },
                reason => {
                    if(typeof reason !== 'string') {
                        if(typeof reason.toString === 'function') {
                            reason = reason.toString();
                        } else {
                            reason = 'Unknown Error'
                        }
                    }

                    errorPrinter(reason)
                }
            );
            return true;
        }

        errorPrinter(NO_CODY_CONNECTION);
    },
    talk: {
        to: {
            Cody: () => {
                if(IIO.cody) {
                    IIO.cody.post(Event.ConfirmTest, {}).then(
                        res => {
                            if(res.status !== 200) {
                                errorPrinter("Did not receive a valid response from cody. " + JSON.stringify(res));
                                return;
                            }

                            codyResponsePrinter(res.data)
                        },
                        reason => {
                            if(typeof reason !== 'string') {
                                if(typeof reason.toString === 'function') {
                                    reason = reason.toString();
                                } else {
                                    reason = 'Unknown Error'
                                }
                            }

                            errorPrinter(reason)
                        }
                    );
                    return true;
                }

                errorPrinter(NO_CODY_CONNECTION);
            }
        }
    },
    trigger: {
        Cody: triggerCody,
    },
    abortSession: () => {
        currentSession = undefined;
        codyResponsePrinter({
            cody: "Yeah, no problem. I stopped my work."
        });
        return true;
    },
    ask: {
        Cody: {
            forHelp: () => {
                codyResponsePrinter({
                    cody: "It's easy. You design the solution and I'll code it for you!",
                    details: [
                        "%cJust select one or more elements on the board and press %cCtrl+G (Cmd+G on Mac)%c or choose %cTrigger Cody%c from context menu of an element.\n\nPressing %cCtrl+Q (Cmd+Q on Mac)%c closes the console.\n\nTo enable Cockpit integration use %c/cockpit https://localhost:<cockpit_port>",
                        'color: #414141',
                        'background-color: rgba(251, 159, 75, 0.2)',
                        'color: #414141',
                        'background-color: rgba(251, 159, 75, 0.2)',
                        'color: #414141',
                        'background-color: rgba(251, 159, 75, 0.2)',
                        'color: #414141',
                        'background-color: rgba(251, 159, 75, 0.2)',
                    ]
                })
            }
        }
    },
    cody: undefined,
    setCodyResponsePrinter: (printer: (res: CodyResponse) => void) => {
        codyResponsePrinter = printer;
    },
    setErrorPrinter: (printer: (error: string) => void) => {
        errorPrinter = printer;
    },
    setWarningPrinter: (printer: (warn: string) => void) => {
        warningPrinter = printer;
    },
}

// @ts-ignore
window.IIO = IIO;

// tslint:disable-next-line:interface-name
export interface IIO {
    connect: Connect;
    disconnect: () => void;
    reply: Reply;
    talk: {
        to: {
            Cody: () => void;
        }
    },
    trigger: {
        Cody: TriggerCody;
    },
    abortSession: () => void;
    ask: {
        Cody: {
            forHelp: () => void;
        }
    }
    cody: CodyServer | undefined;
    setCodyResponsePrinter: (printer: (res: CodyResponse) => void) => void;
    setErrorPrinter: (printer: (error: string) => void) => void;
    setWarningPrinter: (printer: (warn: string) => void) => void;
}

interface Connect {
    Cody: (uri: string, user: string) => void;
}

type Reply = (reply: any) => void;

export type TriggerCody = (boardId: BoardId, nodes: Node[], currentNodeIndex?: number) => boolean;

export enum CodyResponseType {
    Info= 'Info',
    Error = 'Error',
    Warning = 'Warning',
    Question = 'Question',
    StopSession = 'StopSession',
}

export interface CodyResponse {
    cody: string | string[];
    details?: string | string[];
    type?: CodyResponseType;
}

